@media only screen and (max-width: 1024px), screen and (max-height: 660px) {
//@media only screen and (max-width: 1024px) {
  body {
    background: url(../img/bg_1024.png) no-repeat center / cover;
  }
  .wrapper {
    padding: 1.7rem 4rem;
  }
  .header {
    .logo {
      font-size: 90px;
      margin:  0 0 0 -.5rem;
      text-shadow: none;
      letter-spacing: -.2rem;
    }
    .secondary {
      margin-top: .2rem;
      .text-bigger {
        font-size: 40px;
      }
      .text-smaller {
        font-size: 24px;
      }
    }
  }
  .block-form {
    margin-top: 1.5rem;
    width: 400px;
    background: #001d3d;
    box-shadow: 2px 2px 7px #0e2251;
    padding: 15px 12px 15px;

    .block-number {
      .text {
        width: 280px;
      }
      .inputs {
        height: 100%;
        width: 100%;

        .input-telephone {
          height: 50px;
          width: 100%;
          font-size: 24px;
          margin-left: 15px;
        }
      }
      .error {
        position: relative;
        top: .7rem;
        bottom: auto;
        right: auto;
        left: auto;
        padding-bottom: .5rem;

        img {
          width: 25px;
          height: 25px;
        }

        span {
          font-size: 16px;
        }
      }
    }
    .block-currency {
      .text {
        color: #c1fcfe;
        font-size: 18px;
        padding-bottom: .5rem;
      }
      .selectize-control {
        &.selectize-currency {
          .selectize-input {
            width: 100%;
          }
        }
      }
    }
    .block-notify {
      align-items: flex-start;
      span {
        width: 240px;
      }

    }
    .btn-submit {
      display: block;
      width: 300px;
      height: 55px;
      font-size: 22px;
      transition: all .3s ease;
    }
  }
  .footer {
    position: absolute;
    right: 0;
    bottom: 0;
    margin-top: 0;

    .insurance-block {
      .insurance-icon {
        width: 85px;
        height: 92px;
      }
      .insurance-text {
        width: 250px;

        .text-bigger {
          font-size: 28px;
        }
        .text-smaller {
          font-size: 18px;
        }
      }
    }
  }

  .selectize-control {
    &.single {
      .selectize-input:after {
        right: 15px;
      }
    }
    .selectize-input {
      width: 125px;
      height: 50px;
      font-size: 24px;
      padding: 8px 15px;
    }

    .selectize-dropdown-content {
      max-height: 130px;
      font-size: 24px;
    }
    .selectize-dropdown [data-selectable] {
      padding: 10px;
    }
  }
}
@media only screen and (max-width: 875px) {
  .wrapper {
    padding: .5rem 1rem;
  }
}
